import { useNavigate, useParams } from "react-router";
import { get } from "./Request.js";
import { useEffect, useState } from "react";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export function ShowList() {
  const navigate = useNavigate();
  const [rows, setRows] = useState([]);
  const { user } = useParams();

  const jumpinsert = () => {
    navigate(`/frontfinal.github.io/insertpage/${user}`);
  };

  const jumpupdate = (id, address, name, tel) => {
    navigate(`/frontfinal.github.io/updatepage/${id}/${address}/${name}/${tel}/${user}`);
  };

  const jumpdelete = (id) => {
    confirmAlert({
      title: "Notification",
      message: "Are you sure to delete?",
      buttons: [
        {
          label: "Yes",
          onClick: () => {
            navigate(`/frontfinal.github.io/deletepage/${id}/${user}`);
          },
        },
        {
          label: "No",
          onClick: () => {},
        },
      ],
    });
  };

  const logout = () => {
    confirmAlert({
      title: "Notification",
      message: "Are you sure to logout?",
      buttons: [
        {
          label: "Yes",
          onClick: () => {
            window.localStorage.removeItem("jwtToken");
            navigate("/frontfinal.github.io");
          },
        },
        {
          label: "No",
          onClick: () => {},
        },
      ],
    });
  };

  useEffect(() => {
    get("/index.php?action=selects")
      .then((res) => {
        let response = res.data;
        const jumplogin = () => {
          navigate("/frontfinal.github.io/loginpage");
        };
        switch (response.status) {
          case 200:
            const rows = response.result;
            setRows(rows);
            if (window.localStorage) {
              // 儲存到 local storage
              window.localStorage.setItem("jwtToken", response["token"]);
            }
            break;
          case 401:
            toast.error("Timeout, please log in again");
            jumplogin();
            break;
          case 403:
            toast.error("Insufficient permissions!");
            break;
          default:
            toast.error(response.message);
            break;
        }
      })
      .catch((err) => {
        console.error(err);
      });
  }, []);

  return (
    <div>
      <button className="btn btn-info btn-icon-split" onClick={() => jumpinsert()}>
        <span className="icon text-white-50">
          <i className="fas fa-info-circle"></i>
        </span>
        <span className="text">Insert</span>
      </button>
      &nbsp;
      User: {user} Hello!
      &nbsp;
      <button className="btn btn-primary btn-icon-split" onClick={() => logout()}>
        <span className="text">Log out</span>
      </button>
      <table className="show">
        <thead>
          <tr>
            <th>Id</th>
            <th>Address</th>
            <th>Name</th>
            <th>Tel</th>
            <th>Control</th>
          </tr>
        </thead>
        <tbody>
          {rows.map((element) => (
            <tr key={element.id}>
              <td>{element.id}</td>
              <td>{element.address}</td>
              <td>{element.name}</td>
              <td>{element.tel}</td>
              <td>
                <button
                  className="btn btn-success btn-icon-split"
                  onClick={() => jumpupdate(element.id, element.address, element.name, element.tel)}
                >
                  <span className="icon text-white-50">
                    <i className="fas fa-check"></i>
                  </span>
                  <span className="text">Update</span>
                </button>
                <button
                  className="btn btn-danger btn-icon-split"
                  onClick={() => jumpdelete(element.id)}
                >
                  <span className="icon text-white-50">
                    <i className="fas fa-trash"></i>
                  </span>
                  <span className="text">Delete</span>
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <ToastContainer />
    </div>
  );
}
