import { useNavigate, useParams } from "react-router-dom";
//import需要的js檔
export default function StartPage(){
    const navigate = useNavigate();
    const { user } = useParams();
    const handleButtonClick = () => {
        navigate(`/frontfinal.github.io/showpage/${user}`);
    }
    return(
        <div>
            <button onClick={handleButtonClick} className="btn btn-secondary btn-icon-split">
                <span className="icon text-white-50">
                    <i className="fas fa-arrow-right"></i>
                </span>
            <span className="text" onClick={handleButtonClick}>Store Information</span>
            </button>
        </div>
    )
}
