import React, { useEffect, useState } from 'react';
import { post } from './Request.js';
import Qs from 'qs';
import { useNavigate } from 'react-router';
import Swal from 'sweetalert2';

const LoginPage = () => {
  useEffect(() => {
    const handleLogin = () => {
      const data = {
        id: document.getElementById("id").value,
        password: document.getElementById("password").value,
      };
      post('/index.php?action=doLogin', Qs.stringify(data))
        .then(function(resp) {
          let response = resp.data;
          const id = document.getElementById("id").value;
          if (response.status === 200) {
            if (window.localStorage) { // 儲存到 local storage
              window.localStorage.setItem("jwtToken", response.token);
            }
            Swal.fire('Correct!', '', 'success');
            jumpstart(id);
          } else {
            Swal.fire('Mistake!', '', 'error');
            restart();
          }
        })
        .catch(function(err) {
          console.log(err);
        });
    };

    const login = document.getElementById("login");
    login.addEventListener("click", handleLogin);
    return () => {
      login.removeEventListener("click", handleLogin);
    };
  }, []);

  const navigate = useNavigate();
  const jumpstart = (user) => {
    navigate(`/frontfinal.github.io/startpage/${user}`);
  };
  const restart = () => {
    navigate("/frontfinal.github.io");
  };
  return (
    <div>
      <h3 style={{ color: "black" }}>Welcome back!</h3><br />
      <hr style={{ borderWidth: "2px" }}/>
      Account：<input type="text" id="id" className="form-control form-control-user" /><br />
      Password：<input type="password" id="password" className="form-control form-control-user" /><br />
      <hr style={{ borderWidth: "2px" }}/>
      <button id="login" className="btn btn-primary btn-user btn-block">Log in</button>
    </div>
  );
};

export default LoginPage;
