import { post } from "./Request.js";
import Qs from 'qs';
import { useState } from "react";
import { useNavigate, useParams } from "react-router";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export function Showinsert(){
    const navigate = useNavigate();
    const [id, setId] = useState("");
    const [address, setAddress] = useState("");
    const [name, setName] = useState("");
    const [tel, setTel] = useState("");
    const { user } = useParams();

    const jumpreturn = () => {
        navigate(`/frontfinal.github.io/showpage/${user}`);
    };
    const jumplogin = () => {
        window.localStorage.removeItem("jwtToken");
        navigate("/frontfinal.github.io");
    };
    
    const Insert = () => {
        let data = {
            "id": id,
            "address": address,
            "name": name,
            "tel": tel,
        };
        post("/index.php?action=insert", Qs.stringify(data))
        .then(res => {
            let response = res['data'];
            switch(response['status']){
                case 200:
                    // 執行成功後的畫面處理
                    if(window.localStorage){ // 儲存到 local storage
                        window.localStorage.setItem("jwtToken", response['token']);
                    }
                    toast.success("Insert Success");
                    break;
                case 400:
                    toast.error("Data Repeat");
                    break;
                case 401:
                    jumplogin();
                    toast.error("Timeout, please log in again");
                    break;
                case 403:
                    toast.error('Insufficient permissions');
                    break;
            }
        })
        .catch(err => {
            console.error(err);
        });
    };

    return(
        <div>
            Id<input type="text" value={id} onChange={e => setId(e.target.value)} className="form-control form-control-user" />
            Address:<input type="text" value={address} onChange={e => setAddress(e.target.value)} className="form-control form-control-user" />
            Name:<input type="text" value={name} onChange={e => setName(e.target.value)} className="form-control form-control-user" />
            Tel:<input type="text" value={tel} onChange={e => setTel(e.target.value)} className="form-control form-control-user" />
            <button className='btn btn-info btn-icon-split' onClick={Insert}>
                <span className="text">Insert</span>
            </button>
            &nbsp;            
            <button className='btn btn-info btn-icon-split' onClick={jumpreturn}>
                <span className="text">Back</span>
            </button>
            <ToastContainer />
        </div>
    );
}
