import './App.css';
import { Route, Routes } from 'react-router';
import StartPage from './main';
import { Homepage } from './home';
import LoginPage from './doLogin';
import { ShowList } from './controlInfo';
import { Showinsert } from './showinsert';
import { UpdatePage } from './showupdate';
import { Delete } from './dodelete';

function App() {
  return (
    <div className="App">
      <Routes>
        <Route path='/frontfinal.github.io' element={<Homepage />} />
        <Route path='/frontfinal.github.io/loginpage' element={<LoginPage />} />
        <Route path='/frontfinal.github.io/startpage/:user' element={<StartPage />} />
        <Route path='/frontfinal.github.io/showpage/:user' element={<ShowList />} />
        <Route path='/frontfinal.github.io/insertpage/:user' element={<Showinsert />} />
        <Route path='/frontfinal.github.io/updatepage/:id/:address/:name/:tel/:user' element={<UpdatePage />} />
        <Route path='/frontfinal.github.io/deletepage/:id/:user' element={<Delete />} />

      </Routes>
    </div>
  );
}

export default App;
