import { post } from "./Request.js";
import Qs from 'qs';
import { useNavigate, useParams } from "react-router";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


export function Delete() {
  const navigate = useNavigate();
  const {id,user} = useParams();

  const jumpreturn = () => {
    navigate(`/frontfinal.github.io/showpage/${user}`)
  };

  let data = {
    "id": id,
  };
  const jumplogin = () => {
    window.localStorage.removeItem("jwtToken");
    navigate("/frontfinal.github.io");
  };
  post("/index.php?action=delete", Qs.stringify(data))
    .then(res => {
      let response = res['data'];
      switch (response['status']) {
        case 200:
          // 執行成功後的畫面處理
          if (window.localStorage) {
            window.localStorage.setItem("jwtToken", response['token']);
          }
          toast.success("Delete Success");
          break;
        case 401:
          jumplogin();
          toast.error("Timeout, please log in again");
          break;
        case 403:
          toast.error('Insufficient permissions');
          break;
      }
    })
    .catch(err => {
      console.error(err);
    });
    return( 
      <div>
        <button className='btn btn-info btn-icon-split' onClick={jumpreturn}>
        <span className="text">返回</span>
        </button>
        <ToastContainer />
      </div>
    )
}
