import axios from 'axios';

const Request = axios.create({
  baseURL: 'http://localhost/frontfinal/back/public',
  headers: { 'Authorization': window.localStorage.getItem("jwtToken")}
});

export function post(url, data) {
  const ref = {
    headers: {
      'Authorization': window.localStorage.getItem("jwtToken"),
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  };
  return Request.post(url, data, ref);
}

export function get(url) {
  const ref = {
    headers: { 'Authorization': window.localStorage.getItem("jwtToken")}
  };
  return Request.get(url,ref);
}

export default Request;
