import { useState } from "react";
import { useNavigate, useParams } from "react-router";
import { post } from "./Request.js";
import Qs from 'qs';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export function UpdatePage() {
    const navigate = useNavigate();
    const { id, address, name, tel, user } = useParams();
    const [chanegeid, setId] = useState(id);
    const [chanegeaddr, setAddress] = useState(address);
    const [chanegename, setName] = useState(name);
    const [changetel, setTel] = useState(tel);

    const jumpreturn = () => {
        navigate(`/frontfinal.github.io/showpage/${user}`)
    };

    const jumplogin = () => {
        window.localStorage.removeItem("jwtToken");
        navigate("/frontfinal.github.io");
    };

    const Update = () =>{
        let data = {
            "id": chanegeid,
            "address": chanegeaddr,
            "name": chanegename,
            "tel": changetel,
          };
          post("/index.php?action=update", Qs.stringify(data))
          .then(res => {
              let response = res['data']
              switch(response['status']){
                  case 200:
                      // 執行成功後的畫面處理
                      if(window.localStorage){ // 儲存到 local storage
                          window.localStorage.setItem("jwtToken", response['token']);
                      }
                      toast.success("Update Success");
                      break;
                  case 400:
                    toast.error("Data Repeat");
                      break;
                  case 401:
                    jumplogin();
                    toast.error("Timeout, please log in again");
                      break;
                  case 403:
                    toast.error('Insufficient permissions');
                      break;
              }
          })
          .catch(err => {
              console.error(err);
          });
    }
    return(
        <div>
        Id<input type="text" value={chanegeid} onChange={e => setId(e.target.value)} className="form-control form-control-user" />
        Address:<input type="text" value={chanegeaddr} onChange={e => setAddress(e.target.value)} className="form-control form-control-user" />
        Name:<input type="text" value={chanegename} onChange={e => setName(e.target.value)} className="form-control form-control-user" />
        Tel:<input type="text" value={changetel} onChange={e => setTel(e.target.value)} className="form-control form-control-user" />
        <button className='btn btn-info btn-icon-split' onClick={Update}>
            <span className="text">Update</span>
        </button>
        &nbsp;            
        <button className='btn btn-info btn-icon-split' onClick={jumpreturn}>
            <span className="text">Back</span>
        </button>
        <ToastContainer />
        </div>
    )    
}
  
